import { UserStates, DataRefs, Actions } from '../../Stores/Firebase/Selectors'

export function* firebaseStartup() {
  //Check and setup Firebase and permissions
  yield put(FirebaseActions.setFirebaseError(null))

  console.log(Constants.manifest.web.config.firebase)
  firebase.initializeApp(Constants.manifest.web.config.firebase);

  const permitError = yield call(firebaseService.requestUserPermission)
  if (permitError) {
    yield put(FirebaseActions.fetchFailure(permitError))
  }else {
    //yield put(FirebaseActions.firebaseIsLoading(true))
    yield fork(handleAuthState)
    yield fork(handleActions)

    if (firebase.messaging.isSupported()) {
      yield fork(onMessageHandler)
      yield fork(onBackgroundMessageHandler)
    }

    var FirebaseToken = yield call(firebaseService.requestFirebaseToken);
    console.log('Firebase Token: ', FirebaseToken);
    
    //yield fork(testmessage,FirebaseToken)
  }

  console.log('Firebase Startup : Done');
}

