export function* handleAuthState() {
  const authChannel = yield eventChannel((emit) => {
    const subscription = firebase.auth().onAuthStateChanged((user) => {
      if (user == null){
        emit("NONE")
      }else {
        emit(user)
      }
    })
    return () => {
      subscription()
    };
  }, buffers.expanding(1))

  try {
    for (;;) {
      var NewData = {localDataStore:{}}
      const newAuth = yield take(authChannel)
      NewData.firebaseUser = newAuth === "NONE"?null:newAuth;

      if (NewData.firebaseUser) {
        const userData = yield call(firebaseService.actionStarting, DataRefs.USERS, Actions.FETCH, {UserId: newAuth.uid})
        NewData.localDataStore.userData = userData;
      }

      yield put(FirebaseActions.fetchSuccess(NewData))
      console.log("New auth: ", NewData)
    }
  } finally {
    if (yield cancelled()) {
      authChannel.close()
    }
  }
}

export function* handleActions() {
  const channel = yield actionChannel([
    'ACTION_STARTING',
  ])

  for (;;) {
    const action: ACTION_STARTING = yield take(channel)

    //console.log(action.ref, action.action)
    if (Object.values(Actions).includes(action.action)) {
      if (Object.values(DataRefs).includes(action.ref)) {
        const NewData = yield call(firebaseService.actionStarting, action.ref, action.action, action.params)
        //console.log(NewData)
        if (NewData && NewData.error){
          yield put(FirebaseActions.fetchFailure(NewData.error))
        } else {
          yield put(FirebaseActions.fetchSuccess({localDataStore:NewData}))
        }
      }
    }
  }
}

// Notification Handler for then App is in Foreground State
export function* onMessageHandler() {
  const onMessageChannel = yield eventChannel((emit) => {
    const messageListener = firebase.messaging().onMessage((remoteMessage) => {
      //console.log("message ? ", remoteMessage);
      emit(remoteMessage);
    });
    return () => {
      messageListener();
    };
  }, buffers.expanding(1));

  try {
    for (;;) {
      const remoteMessage = yield take(onMessageChannel)
      console.log("New Firebase message: ", remoteMessage)
      if (remoteMessage.notification && remoteMessage.notification.title && remoteMessage.notification.body){
        yield call(firebaseService.wrappedAlert, remoteMessage.notification.title, remoteMessage.notification.body, "Ok", false)
      }else {
        console.log("New Firebase data message: ", remoteMessage)
      }
    }
  } finally {
    if (yield cancelled()) {
      onMessageChannel.close()
    }
  }
}

