//#region Sections
function fetchSections({FarmId, SectionId}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/sections";
    if (SectionId){
      ref += "/" + SectionId
    }
    return firebase.database().ref(ref).once('value').then((_sectionidData) => {
      return {sections: _sectionidData.val()}
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Pigs Error");
    resolve(null)
  })
}
//#endregion

//#region Pens
function fetchPens({FarmId, PenId}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pens";
    if (PenId){
      ref += "/" + PenId
    }
    return firebase.database().ref(ref).once('value').then((_pensData) => {
      if (PenId) {
        var penData = _pensData.val()
        if (!penData.datas) penData.datas = {}
        if (!penData.datas.gcurve) penData.datas.gcurve = "default";

        var pen = {[PenId]: penData};
        return {pens: pen}
      } else {
        return {pens: _pensData.val()}
      }
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Pens Error");
    resolve(null)
  })
}

function createNewPen({FarmId, PenId, SectionId}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      if (PenId){
        var ref2 = ref + "pens/" + PenId + "/"
        firebase.database().ref(ref2).set({
          section: SectionId,
        }).then(() => {
          firebase.database().ref(ref + "sections/" + SectionId + "/pens").update({
            [PenId]: true,
          }).then(() => {
            resolve(null)
          });
        });
      }
    })
  }
}

function deletePen({FarmId, PenId, SectionId}){
  if (FarmId && PenId){
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref + "pens/" + PenId).remove().then(() => {
        if (SectionId){
          firebase.database().ref(ref + "sections/" + SectionId + "/pens/" + PenId).remove().then(() => {
            resolve(null)
          })
        }else {
          resolve(null)
        }
      })
    })
  }
}
//#endregion

//#region Weights
function fetchWeights({FarmId, PenId, SectionId, limitStart = 0, limitTo = 5}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    return new Promise((resolve, reject) => {
      var Data = {}
      if (PenId) {
        ref += "/" + PenId
        firebase.database().ref(ref).orderByChild('time').limitToLast(limitTo).once('value', snapshot => {
          var weighing = {[snapshot.key]:{}}
          snapshot.forEach(c => {
            weighing[snapshot.key][c.key] = c.val()
          })

          resolve({weighings: weighing})
        })
      } else {
        fetchSections({FarmId, SectionId}).then((sections) => {
          Data = {...Data, ...sections}
          
          var itemsProcessed = 0;
          var length = 0;
          Object.keys(Data.sections).forEach(section => {
            length += Object.keys(Data.sections[section].pens).length
            Object.keys(Data.sections[section].pens).forEach(pen => {
              fetchWeights({FarmId:FarmId, PenId:pen, limitStart:0, limitTo:limitTo}).then((weights) => {
                itemsProcessed++

                var newWeights = {...Data.weighings, ...weights.weighings}
                Data = {...Data, weighings:newWeights}
                
                if (itemsProcessed === length){
                  resolve(Data)
                }
              }).catch(error => {
                console.log(error)
                resolve(Data)
              })
            })

          })
        }).catch(error => {
          console.log(error)
        })
      }
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Weights Error");
    resolve(null)
  })
}

function createWeight({FarmId, PenId, Weight}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    if (PenId){
      ref += "/" + PenId + "/"

      return new Promise((resolve, reject) => {
        const weightRef = firebase.database().ref(ref).push()
        const weightid = weightRef.key

        weightRef.set({
          time: firebase.database.ServerValue.TIMESTAMP,
          weight: Weight,
        }).then(() => {
          fetchWeights({FarmId:FarmId, PenId:PenId}).then((weights)=>{
            resolve(weights)
          })
        }).catch(error => {
          console.log("Create Weight Error: ", error);
          resolve(null)
        })
      })
    }
  }
}
//#endregion

//#region Pigs
function fetchPigs({FarmId, TagId}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pigs";
    if (TagId){
      ref += "/" + TagId
    }

    return firebase.database().ref(ref).once('value').then((_pigsData) => {
      if (TagId) {
        var pigData = _pigsData.val()
        if (!pigData) return null;
        if (!pigData.datas) pigData.datas = {}
        if (!pigData.datas.gcurve) pigData.datas.gcurve = "default";

        var pig = {[TagId]: pigData};
        return {pigs: pig}
      } else {
        return {pigs: _pigsData.val()}
      }
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Pigs Error");
    resolve(null)
  })
}

function updatePigPen({FarmId, TagId, PenId, Action}) {
  if (PenId) {
    var ref2 = "farms_data/" + FarmId + "/" + "pens/" + PenId + "/datas"
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref2 + "/count").once('value').then((counts) => {
        var val = counts.val()
        if (!val) val = 0
        if (Action && !isNaN(Action)){
          val = parseInt(val) + parseInt(Action)
        }else {
          val = parseInt(val) +1
        }
        firebase.database().ref(ref2).update({
          count: val,
        }).then(() => {
          fetchPens({FarmId: FarmId, PenId: PenId}).then((pen) => {
            resolve(pen)
          })
        });
      }).catch(error => {
        console.log("Create new pig Error: ", error);
        resolve(null)
      })
    })
  }else {
    return new Promise((resolve, reject) => {
      resolve(null)
    })
  }
}

function updatePigdata({FarmId, TagId, PenId, Action}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      if (TagId){
        var ref2 = ref + "pigs/" + TagId + "/"
        fetchPigs({FarmId: FarmId, TagId: TagId}).then((pig) => {
          if (pig && pig.pigs) {
            pig = pig.pigs[TagId]?pig.pigs[TagId]:null
          }
          if (!pig || !pig.hasOwnProperty('pen')){
            firebase.database().ref(ref2).update({
              pen: 0,
            }).then(() => {
              fetchPigs({FarmId: FarmId}).then((pigs) => {
                resolve(pigs)
              })
            })
          }
          else if (PenId && PenId != pig.pen){
            updatePigPen({FarmId: FarmId, TagId: TagId, PenId: pig.pen, Action:-1}).then((data)=>{
              updatePigPen({FarmId: FarmId, TagId: TagId, PenId: PenId, Action:+1}).then((data)=>{
                firebase.database().ref(ref2).update({
                  pen: PenId
                }).then(() => {
                  fetchPigs({FarmId: FarmId}).then((pigs)=> {
                    resolve(pigs)
                  })
                })
              })
            })
          } else {
            resolve(null)
          }
        })

        /*firebase.database().ref(ref2).update({
          pen: penid ? penid : 0,
        }).then(() => {

          fetchUserPigs(farmid).then((pig)=>{
            fetchUserPens(farmid).then((pens)=>{
              var newData = {...pig, ...pens}
              console.log(newData)
              resolve(pig)
            })
          })
        });*/
      }else {
        console.log("Firebase secive ???")
        return updatePigPen({FarmId: FarmId, TagId: TagId, PenId: PenId, Action:action})
      }
    })
  }
}
