//#region Graphs
function fetchGraphData({FarmId, GraphId}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/graph";
    ref = ref + "/" + GraphId;
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref).once('value').then((graph) => {
        var GraphVal = graph.val()
        if (GraphVal) {
          if (GraphId){
            var Graphs = {[graph.key]: GraphVal}
            resolve({graphs: Graphs})
          }else {
            resolve({graphs: GraphVal})
          }
        } else {
          if (GraphId === "default" || GraphId === "") {
            GraphId = "gcurve"
          }
          firebase.database().ref("farms_data/Agrisys/graph/" + GraphId).once('value').then((Agraph) => {
            var AgraphVal = Agraph.val();
            if (AgraphVal) {
              var Graphs = {[Agraph.key]: AgraphVal}
              resolve({graphs: Graphs})
            }else {
              resolve(null)
            }
          }).catch(error => {
            console.log("Get Agrisys graph Error: ", error);
            resolve(null)
          })
        }
      }).catch(error => {
        console.log("Get farm graph Error: ", error);
        resolve(null)
      })
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Graph Error");
    resolve(null)
  })
}

function updateGraphData({FarmId, Graph}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/graph";
    if (Graph.name){
      ref += "/" + Graph.name + "/"
      return new Promise((resolve, reject) => {
        firebase.database().ref(ref).update({
          data: Graph.data,
          labels: Graph.labels
        }).then(() => {
          fetchGraphData({FarmId: FarmId, GraphId: Graph.name}).then((_Graph)=>{
            resolve(_Graph)
          })
        }).catch(error => {
          console.log("Create Graph Error: ", error);
          resolve(null)
        })
      })
    }
  }
}
